import React, { VFC } from "react";
import { Link, graphql } from "gatsby";
import type { PageProps } from "gatsby";

import Bio from "../components/bio";
import Layout from "../components/layout";
import Seo from "../components/seo";
import { rhythm, scale } from "../utils/typography";
import { BlogPageContext } from "../../gatsby-node";

interface Props {
  contentfulBlogPost: {
    title: string;
    publishDate: string;
    body: {
      childMarkdownRemark: {
        excerpt: string;
        html: string;
      };
    };
  };
  site: {
    siteMetadata: {
      title: string;
    };
  };
}

const BlogPostTemplate: VFC<PageProps<Props, BlogPageContext>> = ({ data, pageContext, location }) => {
  const post = data.contentfulBlogPost;
  const siteTitle = data.site.siteMetadata.title;
  const { previous, next } = pageContext;

  return (
    <Layout location={location} title={siteTitle}>
      <Seo title={post.title} description={post.body.childMarkdownRemark.excerpt} />
      <article>
        <header>
          <h1
            style={{
              marginTop: rhythm(1),
              marginBottom: 0,
            }}
          >
            {post.title}
          </h1>
          <p
            style={{
              ...scale(-1 / 5),
              display: `block`,
              marginBottom: rhythm(1),
            }}
          >
            {post.publishDate}
          </p>
        </header>
        <section dangerouslySetInnerHTML={{ __html: post.body.childMarkdownRemark.html }} />
        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
        <footer>
          <Bio />
        </footer>
      </article>

      <nav>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={`/${previous.slug}`} rel="prev">
                ← {previous.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={`/${next.slug}`} rel="next">
                {next.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    contentfulBlogPost(slug: { eq: $slug }) {
      title
      publishDate(formatString: "YYYY/MM/DD HH:mm:ss Z")
      body {
        childMarkdownRemark {
          excerpt(pruneLength: 160)
          html
        }
      }
    }
  }
`;
